html, body, #root {
  overflow-x: hidden;
}

.brian-navbar {
  width:100%;
  padding:1.25rem 0 !important;
  background-size:100%;
  background-repeat:no-repeat;
  /* background-color:#f7f7f7; */
  background-color:#fff;
  background-image:url('../images/main-background.jpg') !important;
  background-size: 100%;
  background-repeat: no-repeat;
  box-shadow: 0 -7px 10px -10px rgba(0, 0, 0, .95);
  border-bottom:1px solid rgba(0,0,0,.125);
}

.brian-logo-text-top, .brian-logo-text-top-1024 {
  margin-bottom:-.5rem;
  font-family: 'Asap', sans-serif;
  font-weight:500;
  text-transform: capitalize;
}

.brian-logo-text-top {
  font-size:1.25rem;
}

.brian-logo-text-top-1024 {
  font-size:1rem;
}

.brian-logo-text-top-1024 {
  margin-bottom:-.5rem;
  font-family: 'Asap', sans-serif;
  font-weight:500;
  font-size:.75rem;
  text-transform: capitalize;
  text-align:left;
}

.brian-logo-text-bottom {
  font-family: 'Asap', sans-serif;
  font-size:.6rem;
  font-weight:600;
  margin:.25rem 0 0 .25rem;
  text-transform: uppercase;
  text-align:right;
}


.brian-logo-text-bottom-1024 {
  font-family: 'Asap', sans-serif;
  font-size:.5rem;
  font-weight:600;
  margin:.25rem 0 0 .25rem;
  text-transform: uppercase;
  text-align:left;
}

.brian-logo {
  width:4rem;
}

.brian-logo-1024 {
  width:2rem;
}

.brian-logo-text-container {
  text-align:center;
  margin-left: 3rem;
}

.brian-nav-link, .brian-nav-link-1024 {
  position:relative;
  margin:0 1rem;
  padding: .25rem .5rem !important;
  color:#041b3a;
  font-size:.8rem !important;
  text-transform: capitalize;
  color:rgb(74,74,74);
}

.brian-nav-link {
  font-size:.8rem !important;
}

.brian-nav-link-1024 {
  font-size:.8rem !important;
}

.brian-dropdown-link > a {
    color:rgb(74,74,74);
}

.brian-nav-link:hover, .vo-dropdown-link > a:hover {
    color:rgb(74,74,74);
  }

.brian-nav-link:before {
    content: "";
    position: absolute;
    width: 100%;
    height: .2rem;
    bottom: -.2rem;
    left: 0;
    background-color:rgb(74,74,74);
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
    
}

.brian-nav-link:hover:before {
    visibility: visible;
    transform: scaleX(1);
  }

  .nav-email-button {
    position: absolute;
    right: 2rem;
    padding:.25rem 2rem !important;
    background-color: #0947a5 !important;
    border-color: #0947a5 !important;
  }

  .nav-email-button:hover {
    background-color: #003597cb !important;
    border-color: #003597cb !important;
  }

  .flip-arrows {
    width:5rem !important;
    animation:rotation 2s infinite linear;
    -webkit-animation: rotation 2s infinite linear;
  }

  #login-button {
    background: #1ec31e;
    border:1px solid #19a119;
  }

  #login-button:hover {
    background:#19a119;
  }
  
  @keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
    }
  }
  